/** @format */
import React from "react";
import { NavLink } from "react-router-dom";

const About = () => {
  return (
    <>
      <div className="container my-3  ">
        <div className="content">
          <p>
            If you are looking for professional web designer you are at the
            right profile. We will make a responsive and functioning website
            using HTML5, CSS3 and JavaScript as per user requirement.
          </p>
          <br />
          <p>
            Hello Dear! We are
            <strong> frontend developers</strong> and
            <strong> web designers </strong>
            having 2 year of experience.
          </p>
          <br />
          <p>
            We are passionate Digital Content Creator, Creative designers and we
            love to help you to grow your brand and business! We are giving our
            Service at very affordable prize <span> </span>
            <NavLink to="/services" className="about_link">
              <strong> Have a Look </strong>
            </NavLink>{" "}
            <br />
            Kindly Ask to contact us first, before making an order. And if our
            work impress you, we can work together and will create magic. Thank
            you
          </p>
          <br />
          <div className="skills">
            <h2>Our Skills</h2>
            <ul>
              <li>HTML5</li>
              <li>CSS3</li>
              <li>Modern JS</li>
              <li>React</li>
              <li>Git</li>
              <li>Bootstrap</li>
              <li>ES6</li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
};

export default About;
