/** @format */

import React from "react";
import copy from "./Images/copyright.png";

const Footer = () => {
  let year = new Date().getFullYear();
  return (
    <>
      <div className="container-fluid text-center footer d-flex align-items-center justify-content-center d-none d-md-block  ">
        <div className="footer_container ">
          <p>
            {" "}
            <img src={copy} alt="img" className="pe-1" />
            Copyright: RS code {year}
          </p>
        </div>
      </div>
    </>
  );
};

export default Footer;
