/** @format */

import React from "react";
import img1 from "./Images/img1.jpg";
import img2 from "./Images/img2.jpg";
import img3 from "./Images/img3.jpg";

const Home = () => {
  return (
    <>
      <div
        id="carouselExampleDark"
        className="carousel carousel-dark slide home"
        data-bs-ride="carousel"
      >
        <div className="carousel-indicators">
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="0"
            className="active"
            aria-current="true"
            aria-label="Slide 1"
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="1"
            aria-label="Slide 2"
          ></button>
          <button
            type="button"
            data-bs-target="#carouselExampleDark"
            data-bs-slide-to="2"
            aria-label="Slide 3"
          ></button>
        </div>
        <div className="carousel-inner">
          <div
            className="carousel-item active position-relative  "
            data-bs-interval="10000"
          >
            <img
              src={img1}
              className="d-block w-100 opacity-75 imgmedia"
              alt="img"
              height="550px"
            />
            <div className="carousel-caption d-md-block textbox">
              <h1>Welcome to RS code</h1>
              <p>
                We are the team of <strong> frontend developers</strong> and
                <strong> Web designers </strong>
                having quite experience in web-designing.
              </p>
              <hr className="underline d-none d-md-block" />
            </div>
          </div>
          <div
            className="carousel-item  position-relative "
            data-bs-interval="2000"
          >
            <img
              src={img2}
              className="d-block w-100 opacity-75 imgmedia"
              alt="img"
              height="550px"
            />
            <div className="carousel-caption d-md-block textbox ">
              <h1 className="mt-4">Neat and Clean code</h1>

              <hr className="underline  d-none d-md-block" />
            </div>
          </div>
          <div className="carousel-item  position-relative    ">
            <img
              src={img3}
              className="d-block w-100 opacity-75 imgmedia"
              alt="img"
              height="550px"
            />
            <div className="carousel-caption d-md-block textbox ">
              <h1 className="mt-4">Best team of Designer </h1>
              <div>
                <ul className="list-unstyled">
                  <li>
                    <strong> E-commerce disign</strong>
                  </li>
                  <li>
                    <strong>Blogs</strong>
                  </li>
                  <li>
                    <strong>Educational </strong>
                  </li>
                  <li>
                    <strong>Portfolio</strong>
                  </li>
                </ul>
              </div>

              <hr className="underline d-none d-md-block" />
            </div>
          </div>
        </div>
        <button
          className="carousel-control-prev"
          type="button"
          data-bs-target="#carouselExampleDark"
          data-bs-slide="prev"
        >
          <span
            className="carousel-control-prev-icon"
            aria-hidden="true"
          ></span>
          <span className="visually-hidden">Previous</span>
        </button>
        <button
          className="carousel-control-next"
          type="button"
          data-bs-target="#carouselExampleDark"
          data-bs-slide="next"
        >
          <span
            className="carousel-control-next-icon"
            aria-hidden="true"
          ></span>
          <span className="visually-hidden">Next</span>
        </button>
      </div>
    </>
  );
};

export default Home;
